import React from 'react';
import styles from '../styling/AppBarHeader.module.css';
import { Typography, Button } from '@mui/material';
import appbarLogo from '../assets/ProfileLogo.png';
import PhoneIcon from '@mui/icons-material/Phone';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';

const AppBarHeader = () => {
    return (
        <div className={styles.appBarHeader}>
            <img className={styles.profileLogo} src={appbarLogo} alt='Deep Profile' />
            <div className={styles.contactContainer}>
                <Button href='tel:8420057039' className={styles.phoneBtn}>
                    <PhoneIcon />
                </Button>
                <Button href='mailto:deep.ksarkar1211@gmail.com' className={styles.emailBtn}>
                    <MailOutlineIcon />
                </Button>
                <Button href='https://linkedin.com/in/deep-ksarkar' className={styles.linkedinBtn}>
                    <LinkedInIcon />
                </Button>
                <Button href='https://github.com/c0dE3P' className={styles.githubBtn}>
                    <GitHubIcon />
                </Button>
            </div>
        </div>
    )
}

export default AppBarHeader;